.transactionHistory {
  width: 100%;
  min-width: 300px;
  text-align: center;
  box-shadow: 1px 1px 2px 3px #dddddd;
}

.transactionHistory thead {
  background-color: dodgerblue;
  color: white;
}

.transactionHistory tr {
  line-height: 36px;
}

.transactionHistory tr:nth-child(2n) {
  background-color: #eeeeee;
}
